@import "normalize.scss";



body {
	background-image: url("../img/picture/background.png");
	background-size: cover;
	height: 100%;
	width: 100%;
}


.narrow {
	height: 100vh;
	width: 80%;
	margin: 20px auto;
	background: rgba( 255, 255, 255, 0.15 );
	box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
	backdrop-filter: blur( 7.0px );
	-webkit-backdrop-filter: blur( 7.0px );
	border-radius: 10px;
  }
  
.display-container {
	height: 80%;
	display: grid;
	grid-template-columns: 50% 50%;
  }

.picture-box {
	height: 100%;
	overflow: hidden;
}

.pic {
	display: block;
	width: 90%;
	margin-top: 130%;
	margin-left: 5%;
	margin-right: 5%;
	transition: all 8s linear;
}




.button-container {
	height: 20%;
	display: flex;
	justify-content: space-around;
	align-items: center;
  }
  
  
.key {
	height: 60%;
	width: 5.5%;
	border-radius: 4px;
	background:rgba( 255, 255, 255, 0);
	border: solid 1px black;
	box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	color: #fff;
	transition: all 0.07s;
  }
  
.key_active {
	background: #000;
	transform: scale(1.2);
}


.key:hover {
	-webkit-box-shadow: 0px 0px 5px 4px #F6A945; 
	box-shadow: 0px 0px 5px 4px #F6A945;
	}